type envData = {albyCallbackUrl: string}

const data: {development: envData, production: envData, test: envData} = {
  development: {
    albyCallbackUrl: 'https://getalby.com/oauth?client_id=muUGbCrFMs&response_type=code&redirect_uri=http://localhost:8080/auth/callback&scope=account:read%20invoices:create%20invoices:read%20transactions:read%20balance:read%20payments:send'
  },
  production: {
    albyCallbackUrl: 'https://getalby.com/oauth?client_id=iYdnG2PtKh&response_type=code&redirect_uri=https://deimox-team.github.io/postrich/auth_callback&scope=account:read%20invoices:create%20invoices:read%20transactions:read%20balance:read%20payments:send'
  },
  test: {
    albyCallbackUrl: 'c'
  }
}

type EnvName = 'production' | 'development' | 'test'

export default function getEnvItem(key: keyof envData) {
  const env = process.env.NODE_ENV
  return (data[env as EnvName][key])
}