import { Dispatch } from '@reduxjs/toolkit';
import { getPublicKey, nip19 } from 'nostr-tools';
import secureLocalStorage from 'react-secure-storage';
import LSKeys from '../../data/constants/permaStorageKeys';
import { setLoginMethod, setNpub } from '../../data/store/slices/userSlice';
import { store } from '../../data/store/store';
import { getEnhancedWindow } from '../../data/types/NIP07';
import { UserType } from '../../data/types/UserType';

export function getPubKey() {
  const user = store.getState().user
  const permaStored = secureLocalStorage.getItem(LSKeys.userPrivKey)
  if (permaStored) {
    return getPublicKey(nip19.decode(permaStored as string).data as string)
  }
  
  return user.npub || null
}

export function setUser(dispatch: Dispatch, data: { loginMethod: UserType['loginMethod'], nKey?: string }) {
  dispatch(setLoginMethod(data.loginMethod))
  if (data.loginMethod === 'none') return false
  else if (data.loginMethod === 'ext') {
    const eWindow = getEnhancedWindow()
    eWindow.nostr.getPublicKey().then(res => dispatch(setNpub(res)))
    return true
  }
  else {
    let pubKey
    if (data.loginMethod === 'nsec' && data.nKey) {
      secureLocalStorage.setItem(LSKeys.userPrivKey, data.nKey)
      pubKey = getPublicKey( nip19.decode(data.nKey).data as string)
    } 
    else if (data.loginMethod === 'npub' && data.nKey) {
      pubKey = data.nKey
    }

    if (pubKey) {
      dispatch(setNpub(pubKey))
      return true
    }
    else {
      return false
    }
  }
}