import { PrimeIcons } from "primereact/api";
import { Button } from "primereact/button";
import { Menu } from "primereact/menu";
import { useRef } from "react";
import { useNavigate } from "react-router";
import { webRouteMap } from "../../router";
import "./style.scss";

export const NavbarItems = ["feed", "inbox", "updates"];

export function WebNavbar() {
  const menuRef = useRef<Menu>(null);
  const nav = useNavigate();
  const items = [
    {
      label: "About",
      icon: PrimeIcons.QUESTION,
    },
    {
      label: "Download",
      icon: PrimeIcons.DOWNLOAD,
      onclick: () => nav(webRouteMap.install.full),
    },
    { label: "Contribute", icon: PrimeIcons.SITEMAP },
  ];

  return (
    <div className="webnavbar">
      <div className="logo">
        <h2 className="appname">Postrich</h2>
      </div>
      <div className="webitem menu">
        <Menu
          model={items}
          popup
          ref={menuRef}
          id="popup_menu"
          popupAlignment="right"
        />
        <Button
          icon={PrimeIcons.BARS}
          onClick={(event) => menuRef.current?.toggle(event)}
          aria-controls="popup_menu"
          aria-haspopup
        />
      </div>
    </div>
  );
}
