import { InputText } from "primereact/inputtext";
import { classNames } from "primereact/utils";
import {
  Control,
  Controller,
  FieldErrors,
  FieldValues,
  RegisterOptions,
} from "react-hook-form";
import { GenericObject } from "../../../../data/types/utils";
import "./style.scss";

export interface FieldProps {
  name: string;
  label?: string | JSX.Element;
  control?: Control<GenericObject>;
  rules?: Omit<
    RegisterOptions<FieldValues, "value">,
    "valueAsNumber" | "valueAsDate" | "setValueAs" | "disabled"
  >;
  errors: FieldErrors<{ [key: string]: unknown }>;
}

export function FormField({ name, label, control, rules, errors }: FieldProps) {
  const getFormErrorMessage = (name: string) => {
    return errors[name] ? (
      <small className="p-error">{errors[name]?.message}</small>
    ) : (
      <small className="p-error">&nbsp;</small>
    );
  };

  return (
    <Controller
      name={name}
      control={control}
      rules={rules}
      render={({ field, fieldState }) => (
        <>
          <label
            htmlFor={field.name}
            className={classNames({ "p-error": errors.value })}
          ></label>
          <span className="p-float-label">
            <InputText
              id={field.name}
              value={field.value}
              className={classNames({ "p-invalid": fieldState.error })}
              onChange={(e) => field.onChange(e.target.value)}
            />
            {label ? <label htmlFor={field.name}>{label}</label> : null}
          </span>
          {getFormErrorMessage(field.name)}
        </>
      )}
    />
  );
}
