import { Button } from "primereact/button";
import { Toast } from "primereact/toast";
import { useRef } from "react";
import { FieldValues, useForm } from "react-hook-form";
import { useAppDispatch } from "../../../../data/store/store";
import { UserType } from "../../../../data/types/UserType";
import { GenericObject } from "../../../../data/types/utils";
import { setUser } from "../../../../lib/services/user";
import { FormField } from "../../../common/atoms/FormField";

export default function SignInForm() {
  const toastRef = useRef<Toast>(null);

  const defaultValues: GenericObject = {
    nostrKey: "",
  };

  const {
    control,
    formState: { errors },
    handleSubmit,
  } = useForm({ defaultValues });

  const dispatch = useAppDispatch();
  const onSignIn = (data: FieldValues) => {
    try {
      let loginMethod: UserType["loginMethod"] = "none";
      if (data.nostrKey.substring(0, 4) === "nsec") {
        loginMethod = "nsec";
      } else if (data.nostrKey.substring(0, 4) === "npub") {
        loginMethod = "npub";
      }
      setUser(dispatch, { loginMethod, nKey: data.nostrKey });
      toastRef.current?.show({
        severity: "success",
        summary: "Login success",
        detail: "You may change your account in the settings",
      });
    } catch (error: unknown | { [key: string]: any }) {
      toastRef.current?.show({
        severity: "error",
        summary: "Provided key is invalid!",
        detail: "You must provide a valid nsec",
      });
      console.log({ error });
    }
  };
  return (
    <div className="signin-form">
      <Toast ref={toastRef} />
      <form onSubmit={handleSubmit(onSignIn)}>
        <FormField
          name="nostrKey"
          label="Nostr key (nsec)"
          control={control}
          errors={errors}
        />
        <Button label="Sign in" type="submit" />
      </form>
    </div>
  );
}
