import { PayloadAction, createSlice } from '@reduxjs/toolkit'
import { predefinedRelays } from '../../constants/relays'
import { RelayType } from '../../types/RelayType'

export const relaySlice = createSlice({
  name: 'relay',
  initialState: {
    relays: predefinedRelays,
  } as {relays: RelayType[]},
  reducers: {
    addRelay: (state, action: PayloadAction<RelayType>) => {
      state.relays = [...state.relays, action.payload]
    },
    removeRelay: (state, action: PayloadAction<RelayType['address']>) => {
      const relayIndex = state.relays.findIndex(r => r.address === action.payload)
      if (relayIndex !== -1) {
        state.relays.splice(relayIndex, 1)
      }
    },
    updateRelay: (state, action: PayloadAction<RelayType>) => {
      const relayIndex = state.relays.findIndex(r => r.address === action.payload.address)
      if (relayIndex !== -1) {
        state.relays[relayIndex] = action.payload
      }
    },
  },
})

export const { addRelay } = relaySlice.actions

export default relaySlice.reducer