import { SimplePool } from "nostr-tools";
import { PrimeIcons } from "primereact/api";
import { Avatar } from "primereact/avatar";
import { Button } from "primereact/button";
import { useEffect } from "react";
import { setProfile } from "../../../../data/store/slices/userSlice";
import { useAppDispatch, useAppSelector } from "../../../../data/store/store";
import { getPubKey } from "../../../../lib/services/user";
import "./style.scss";

export const NavbarItems = ["feed", "inbox", "updates"];

export interface HeaderProps {
  title: string;
}

export function Header({ title }: HeaderProps) {
  const pool = new SimplePool();
  const relays = useAppSelector((state) => state.relay.relays);
  const relayList = relays.map((r) => r.address);
  const dispatch = useAppDispatch();
  const profilePicture = useAppSelector((state) => state.user.profile.picture);

  useEffect(() => {
    const pubKey = getPubKey();
    if (pubKey) {
      pool
        .list(relayList, [{ kinds: [0], authors: [pubKey] }])
        .then((events) => {
          if (events[0]) {
            dispatch(setProfile(JSON.parse(events[0].content)));
          }
        });
    }
  }, [null]);

  return (
    <div id="header">
      <Avatar
        className="profile-link"
        icon={PrimeIcons.USER}
        image={profilePicture}
      />
      <h2 className="title">{title}</h2>
      <div className="controls">
        <Button rounded text icon={PrimeIcons.COG} size="large" />
      </div>
    </div>
  );
}
