import { generatePrivateKey, nip19 } from "nostr-tools";
import { Button } from "primereact/button";
import { Checkbox } from "primereact/checkbox";
import { InputTextarea } from "primereact/inputtextarea";
import { useMemo, useState } from "react";
import { redirect } from "react-router";
import { useAppDispatch } from "../../../../data/store/store";
import { setUser } from "../../../../lib/services/user";
import "./style.scss";

export function SignUp() {
  const [agreed, setAgreed] = useState(false);
  const nsec = useMemo(() => {
    return nip19.nsecEncode(generatePrivateKey());
  }, [null]);
  const dispatch = useAppDispatch();
  const onSignIn = () => {
    setUser(dispatch, { loginMethod: "nsec", nKey: nsec });
    redirect("/");
  };

  return (
    <div className="page sign-up">
      <h1>Sign up</h1>
      <p className="reminder"></p>
      <InputTextarea value={nsec} onChange={() => {}} />
      <div>
        <Checkbox
          inputId="risk-agree"
          name="risk-agree"
          value="Cheese"
          onChange={() => setAgreed(!agreed)}
          checked={agreed}
        />
        <label htmlFor="risk-agree" className="ml-2">
          I understand
        </label>
      </div>
      <Button label="Sign in" onClick={onSignIn} />
    </div>
  );
}
