import { createBrowserRouter, redirect } from "react-router-dom";
import { getPubKey } from "../../lib/services/user";
import { welcomeLoader } from "../../lib/services/welcome";
import App from "./App";
import { Error } from "./pages/Error";
import { Feed } from "./pages/Feed";
import { Inbox } from "./pages/Inbox";
import { Main } from "./pages/Main";
import { PublicHome } from "./pages/PublicHome";
import { SignIn } from "./pages/SignIn";
import { SignUp } from "./pages/SignUp";

export const rouwteMap = {
  signIn: { prefix: "/auth", path: "/sign-in", full: "/auth/sign-in" },
  signUp: { prefix: "/auth", path: "/sign-up", full: "/auth/sign-up" },
  extCallback: { prefix: "/auth", path: "/callback", full: "/auth/callback" },
  main: { prefix: null, path: "/", full: "/" },
  feed: { prefix: null, path: "/feed", full: "/feed" },
  inbox: { prefix: null, path: "/inbox", full: "/inbox" },
  updates: { prefix: null, path: "/updates", full: "/updates" },
  explore: { prefix: null, path: "/explore", full: "/explore" },
};

export const routeMap = {
  main: { path: "/", el: <Main />, title: "Welcome" },
  feed: { path: "/feed", el: <Feed />, title: "Feed" },
  explore: { path: "/explore", el: <Error />, title: "Explore" },
  inbox: { path: "/inbox", el: <Inbox />, title: "Inbox" },
  updates: { path: "/updates", el: <Error />, title: "Updates" },
  signIn: { path: "/auth/sign-in", el: <SignIn />, title: "Sign in" },
  signUp: { path: "/auth/sign-up", el: <SignUp />, title: "Sign up" },
};

const appRouter = createBrowserRouter(
  [
    {
      path: "/",
      element: <App />,
      errorElement: <Error />,
      loader: () => {
        const pubKey = getPubKey();

        if (pubKey) {
          return null;
        } else return redirect(routeMap.signIn.path);
      },
      children: [
        {
          path: routeMap.main.path,
          element: routeMap.main.el,
          loader: welcomeLoader,
        },
        {
          path: routeMap.feed.path,
          element: routeMap.feed.el,
        },
        {
          path: routeMap.explore.path,
          element: routeMap.explore.el,
        },
        {
          path: routeMap.inbox.path,
          element: routeMap.inbox.el,
        },
        {
          path: routeMap.updates.path,
          element: routeMap.updates.el,
        },
      ],
    },
    {
      path: "/auth",
      element: <PublicHome />,
      children: [
        {
          path: routeMap.signIn.path,
          element: routeMap.signIn.el,
          loader: () => {
            const pubKey = getPubKey();
            if (pubKey) {
              return redirect(routeMap.main.path);
            }
            return null;
          },
        },
        {
          path: routeMap.signUp.path,
          element: routeMap.signUp.el,
        },
        {
          path: "callback",
          loader: () => redirect(routeMap.main.path),
        },
      ],
    },
  ],
  {}
);

export default appRouter;
