import "primeicons/primeicons.css";
import { PrimeReactProvider } from "primereact/api";
import "primereact/resources/primereact.min.css";
import "primereact/resources/themes/mdc-dark-deeppurple/theme.css";
import "primereact/resources/themes/mdc-light-deeppurple/theme.css";
import React from "react";
import ReactDOM from "react-dom/client";
import { Provider } from "react-redux";
import { RouterProvider } from "react-router";
import { PersistGate } from "redux-persist/integration/react";
import { persistor, store } from "./data/store/store";
import appRouter from "./gui/app/router";
import webRouter from "./gui/web/router";
import isPwaInstalled from "./lib/utils/installed";

const router = isPwaInstalled() ? appRouter : webRouter;
const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);

root.render(
  <React.StrictMode>
    <Provider store={store}>
      <PrimeReactProvider>
        <PersistGate loading={null} persistor={persistor}>
          <RouterProvider router={router} />
        </PersistGate>
      </PrimeReactProvider>
    </Provider>
  </React.StrictMode>
);
