import { PayloadAction, createSlice } from '@reduxjs/toolkit'
import { UserType } from '../../types/UserType'

export const userSlice = createSlice({
  name: 'user',
  initialState: {
    loginMethod: 'none',
    npub: '',
    profile: {}
  } as UserType,
  reducers: {
    setLoginMethod: (state, action: PayloadAction<UserType['loginMethod']>) => {
      state.loginMethod = action.payload
    },
    setNpub: (state, action: PayloadAction<UserType['npub']>) => {
      state.npub = action.payload
    },
    setProfile: (state, action: PayloadAction<UserType['profile']>) => {
      state.profile = action.payload
    },
  },
})

export const { setLoginMethod, setNpub, setProfile } = userSlice.actions

export default userSlice.reducer